// –Ü–º–ø–æ—Ä—Ç –Ω–µ–æ–±—Ö—ñ–¥–Ω–∏—Ö –º–æ–¥—É–ª—ñ–≤ —ñ –±—ñ–±–ª—ñ–æ—Ç–µ–∫
import { useContext, useState } from 'react';
import cn from 'classnames';
import s from './SpecializationList.module.scss';
import { UserIdContext } from '../../../context/context';

// –ö–æ–º–ø–æ–Ω–µ–Ω—Ç SpecializationList –¥–ª—è –≤–∏–±–æ—Ä—É —Å–ø–µ—Ü—ñ–∞–ª—ñ–∑–∞—Ü—ñ—ó
function SpecializationList({ specializations, handleClickSpecialization }) {
  const [activeSpecialization, setActiveSpecialization] = useState(null); // –°—Ç–∞–Ω –¥–ª—è –∑–±–µ—Ä–µ–∂–µ–Ω–Ω—è –∞–∫—Ç–∏–≤–Ω–æ—ó —Å–ø–µ—Ü—ñ–∞–ª—ñ–∑–∞—Ü—ñ—ó
  const userId = useContext(UserIdContext); // –û—Ç—Ä–∏–º–∞–Ω–Ω—è userId –∑ –∫–æ–Ω—Ç–µ–∫—Å—Ç—É

  // –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –æ–±—Ä–æ–±–∫–∏ –≤–∏–±–æ—Ä—É —Å–ø–µ—Ü—ñ–∞–ª—ñ–∑–∞—Ü—ñ—ó
  const onClickSpecialization = (specialization) => {
    setActiveSpecialization(specialization); // –û–Ω–æ–≤–ª–µ–Ω–Ω—è –∞–∫—Ç–∏–≤–Ω–æ—ó —Å–ø–µ—Ü—ñ–∞–ª—ñ–∑–∞—Ü—ñ—ó
    handleClickSpecialization(specialization); // –í–∏–∫–ª–∏–∫ —Ñ—É–Ω–∫—Ü—ñ—ó –¥–ª—è –æ–±—Ä–æ–±–∫–∏ –≤–∏–±–æ—Ä—É
  };

  // –ö–ª–∞—Å–∏ –¥–ª—è –µ–ª–µ–º–µ–Ω—Ç–∞ —Å–ø–∏—Å–∫—É —Å–ø–µ—Ü—ñ–∞–ª—ñ–∑–∞—Ü—ñ–π
  const itemClass = cn(s.specialization__item, {
    [s.browser]: !userId, // –î–æ–¥–∞—Ç–æ–∫ –∫–ª–∞—Å—É, —è–∫—â–æ userId –≤—ñ–¥—Å—É—Ç–Ω—ñ–π
  });

  return (
    <div className={s.specialization}>
      <h2 tabIndex={0} className={s.specialization__title}>–û–±–µ—Ä—ñ—Ç—å —Å–ø–µ—Ü—ñ–∞–ª—ñ–∑–∞—Ü—ñ—é:</h2>
      <ul className={s.specialization__list}>
        {specializations.map((specialization) => (
          <li
            key={specialization}
            onClick={() => onClickSpecialization(specialization)} // –û–±—Ä–æ–±–∫–∞ –∫–ª—ñ–∫—É
            onKeyDown={(e) => {
              if (e.key === 'Enter' || e.key === ' ') {
                e.preventDefault(); // –ó–∞–ø–æ–±—ñ–≥–∞–Ω–Ω—è —Å—Ç–∞–Ω–¥–∞—Ä—Ç–Ω–æ—ó –ø–æ–≤–µ–¥—ñ–Ω–∫–∏
                onClickSpecialization(specialization); // –û–±—Ä–æ–±–∫–∞ –∫–ª–∞–≤—ñ–∞—Ç—É—Ä–Ω–æ–≥–æ –≤–≤–æ–¥—É
              }
            }}
            tabIndex={0} 
            role="button" 
            className={cn(itemClass, {
              [s.specialization__item_active]: specialization === activeSpecialization, // –î–æ–¥–∞—Ç–æ–∫ –∫–ª–∞—Å—É –¥–ª—è –∞–∫—Ç–∏–≤–Ω–æ—ó —Å–ø–µ—Ü—ñ–∞–ª—ñ–∑–∞—Ü—ñ—ó
              [s.browser_active]: specialization === activeSpecialization,
            })}
            aria-pressed={specialization === activeSpecialization} // –î–æ–¥–∞—Ç–∫–æ–≤–∞ —ñ–Ω—Ñ–æ—Ä–º–∞—Ü—ñ—è –ø—Ä–æ –∞–∫—Ç–∏–≤–Ω—ñ—Å—Ç—å
            aria-label={`–û–±–µ—Ä—ñ—Ç—å —Å–ø–µ—Ü—ñ–∞–ª—ñ–∑–∞—Ü—ñ—é ${specialization}`} // –û–ø–∏—Å –¥–ª—è –∞—Å–∏—Å—Ç–∏–≤–Ω–∏—Ö —Ç–µ—Ö–Ω–æ–ª–æ–≥—ñ–π
          >
            {specialization}
          </li>
        ))}
      </ul>
    </div>
  );
}

export default SpecializationList;
