import axios from "axios"; // –Ü–º–ø–æ—Ä—Ç—É—î—Ç—å—Å—è –±—ñ–±–ª—ñ–æ—Ç–µ–∫–∞ axios –¥–ª—è —Ä–æ–±–æ—Ç–∏ –∑ HTTP –∑–∞–ø–∏—Ç–∞–º–∏
import { instance } from "./api"; // –Ü–º–ø–æ—Ä—Ç—É—î—Ç—å—Å—è –Ω–∞–ª–∞—à—Ç–æ–≤–∞–Ω–∏–π –µ–∫–∑–µ–º–ø–ª—è—Ä axios –¥–ª—è API –∑–∞–ø–∏—Ç—ñ–≤

// –¢–æ–∫–µ–Ω –±–æ—Ç–∞ Telegram –¥–ª—è –¥–æ—Å—Ç—É–ø—É –¥–æ API
const TELEGRAM_BOT_TOKEN = '7528553042:AAH--t6VitbS4Su4pDKKsODm1UWkzqnQdMo';
// URL –¥–ª—è –Ω–∞–¥—Å–∏–ª–∞–Ω–Ω—è –ø–æ–≤—ñ–¥–æ–º–ª–µ–Ω—å —á–µ—Ä–µ–∑ Telegram API
const TELEGRAM_API_URL = `https://api.telegram.org/bot${TELEGRAM_BOT_TOKEN}/sendMessage`;

// –û–±'—î–∫—Ç registrationAPI, —â–æ –º—ñ—Å—Ç–∏—Ç—å –º–µ—Ç–æ–¥–∏ –¥–ª—è —Ä–µ—î—Å—Ç—Ä–∞—Ü—ñ—ó —Ç–∞ —Ä–æ–±–æ—Ç–∏ –∑ –ª—ñ–∫–∞—Ä—è–º–∏
export const registrationAPI = {
  // –ú–µ—Ç–æ–¥ –¥–ª—è –¥–æ–¥–∞–≤–∞–Ω–Ω—è –Ω–æ–≤–æ–≥–æ –∑–∞–ø–∏—Å—É
  addRecord(data) {
    return instance
      .post(`records`, data) 
      .then(response => response.data); 
  },

  // –ú–µ—Ç–æ–¥ –¥–ª—è –Ω–∞–¥—Å–∏–ª–∞–Ω–Ω—è –ø–æ–≤—ñ–¥–æ–º–ª–µ–Ω–Ω—è –ª—ñ–∫–∞—Ä—é
  sendMessageToDoctor(chat_id, text) {
    return axios.post(TELEGRAM_API_URL, { 
      chat_id,
      text 
    });
  },

  // –ú–µ—Ç–æ–¥ –¥–ª—è –æ—Ç—Ä–∏–º–∞–Ω–Ω—è –≤—Å—ñ—Ö —Å–ø–µ—Ü—ñ–∞–ª—ñ–∑–∞—Ü—ñ–π –ª—ñ–∫–∞—Ä—ñ–≤
  getSpecializations() {
    return instance
      .get(`doctors/specializations/all`) 
      .then(response => response.data); 
  },

  // –ú–µ—Ç–æ–¥ –¥–ª—è –æ—Ç—Ä–∏–º–∞–Ω–Ω—è –ª—ñ–∫–∞—Ä—ñ–≤ –∑–∞ —Å–ø–µ—Ü—ñ–∞–ª—ñ–∑–∞—Ü—ñ—î—é
  getDoctors(specialization) {
    return instance
      .get(`doctors/${specialization}`) 
      .then(response => response.data); 
  },

  // –ú–µ—Ç–æ–¥ –¥–ª—è –æ—Ç—Ä–∏–º–∞–Ω–Ω—è –≥—Ä–∞—Ñ—ñ–∫–∞ –ª—ñ–∫–∞—Ä—è –∑–∞ ID
  getSchedule(doctorId) {
    return instance
      .get(`doctor/${doctorId}/schedule`) 
      .then(response => response.data); 
  },
};
