// –Ü–º–ø–æ—Ä—Ç –Ω–µ–æ–±—Ö—ñ–¥–Ω–∏—Ö –º–æ–¥—É–ª—ñ–≤ —ñ –±—ñ–±–ª—ñ–æ—Ç–µ–∫
import { useCallback, useContext, useEffect, useState } from 'react';
import s from './PatientInfo.module.scss';
import cn from 'classnames';
import { UserIdContext } from '../../../context/context';

// –ö–æ–º–ø–æ–Ω–µ–Ω—Ç PatientInfo –¥–ª—è –∑–±–æ—Ä—É —ñ–Ω—Ñ–æ—Ä–º–∞—Ü—ñ—ó –ø—Ä–æ –ø–∞—Ü—ñ—î–Ω—Ç–∞
function PatientInfo({ patientName, setPatientName, patientPhone, setPatientPhone, handleDataInput }) {
  const [isNameValid, setIsNameValid] = useState(true); // –°—Ç–∞–Ω –¥–ª—è –ø–µ—Ä–µ–≤—ñ—Ä–∫–∏ –≤–∞–ª—ñ–¥–Ω–æ—Å—Ç—ñ —ñ–º–µ–Ω—ñ
  const [isPhoneValid, setIsPhoneValid] = useState(true); // –°—Ç–∞–Ω –¥–ª—è –ø–µ—Ä–µ–≤—ñ—Ä–∫–∏ –≤–∞–ª—ñ–¥–Ω–æ—Å—Ç—ñ —Ç–µ–ª–µ—Ñ–æ–Ω—É

  const userId = useContext(UserIdContext); // –û—Ç—Ä–∏–º–∞–Ω–Ω—è userId –∑ –∫–æ–Ω—Ç–µ–∫—Å—Ç—É

  // –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –≤–∞–ª—ñ–¥–∞—Ü—ñ—ó —ñ–º–µ–Ω—ñ
  const validateName = useCallback(() => {
    setIsNameValid(patientName.trim() !== ''); // –ü–µ—Ä–µ–≤—ñ—Ä–∫–∞, —â–æ —ñ–º'—è –Ω–µ –ø—É—Å—Ç–µ
  }, [patientName]);

  // –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –≤–∞–ª—ñ–¥–∞—Ü—ñ—ó —Ç–µ–ª–µ—Ñ–æ–Ω—É
  const validatePhone = useCallback(() => {
    const phoneRegex = /^380\d{9}$/; // –†–µ–≥—É–ª—è—Ä–Ω–∏–π –≤–∏—Ä–∞–∑ –¥–ª—è —É–∫—Ä–∞—ó–Ω—Å—å–∫–æ–≥–æ –Ω–æ–º–µ—Ä–∞ —Ç–µ–ª–µ—Ñ–æ–Ω—É
    setIsPhoneValid(phoneRegex.test(patientPhone)); // –í—Å—Ç–∞–Ω–æ–≤–ª–µ–Ω–Ω—è –≤–∞–ª—ñ–¥–Ω–æ—Å—Ç—ñ —Ç–µ–ª–µ—Ñ–æ–Ω—É
  }, [patientPhone]);

  // –í–∏–∫–æ–Ω–∞–Ω–Ω—è –≤–∞–ª—ñ–¥–∞—Ü—ñ–π –ø—Ä–∏ –∑–º—ñ–Ω—ñ —ñ–º–µ–Ω—ñ –∞–±–æ —Ç–µ–ª–µ—Ñ–æ–Ω—É
  useEffect(() => {
    validateName();
    validatePhone();
    handleDataInput(); // –í–∏–∫–ª–∏–∫ —Ñ—É–Ω–∫—Ü—ñ—ó –¥–ª—è –æ–±—Ä–æ–±–∫–∏ –≤–≤–µ–¥–µ–Ω–∏—Ö –¥–∞–Ω–∏—Ö
  }, [patientName, patientPhone, handleDataInput, validateName, validatePhone]);

  // –û–±—Ä–æ–±–∫–∞ –∑–º—ñ–Ω–∏ —ñ–º–µ–Ω—ñ
  const handleChangeName = (e) => {
    setPatientName(e.target.value); // –û–Ω–æ–≤–ª–µ–Ω–Ω—è —Å—Ç–∞–Ω—É —ñ–º–µ–Ω—ñ
  };

  // –û–±—Ä–æ–±–∫–∞ –∑–º—ñ–Ω–∏ —Ç–µ–ª–µ—Ñ–æ–Ω—É
  const handleChangePhone = (e) => {
    setPatientPhone(e.target.value); // –û–Ω–æ–≤–ª–µ–Ω–Ω—è —Å—Ç–∞–Ω—É —Ç–µ–ª–µ—Ñ–æ–Ω—É
  };

  // –ö–ª–∞—Å–∏ –¥–ª—è –±–ª–æ–∫–∞ —ñ–Ω—Ñ–æ—Ä–º–∞—Ü—ñ—ó
  const blockClass = cn(s.info__block, {
    [s.browser]: !userId, // –î–æ–¥–∞—î—Ç—å—Å—è –∫–ª–∞—Å, —è–∫—â–æ userId –≤—ñ–¥—Å—É—Ç–Ω—ñ–π
  });

  return (
    <div className={s.info}>
      <h1 tabIndex={0} className={s.info__title}>–ó–∞–ø–∏—Å</h1>
      <div className={blockClass}>
        <label>
          <span>–ü–Ü–ë:</span>
          <input
            placeholder="–ü–Ü–ë"
            type="text"
            value={patientName}
            onChange={handleChangeName} // –û–±—Ä–æ–±–∫–∞ –∑–º—ñ–Ω–∏ —ñ–º–µ–Ω—ñ
            onBlur={validateName} // –í–∞–ª—ñ–¥–∞—Ü—ñ—è –ø—Ä–∏ –≤—Ç—Ä–∞—Ç—ñ —Ñ–æ–∫—É—Å—É
            required
            aria-invalid={!isNameValid} // –î–æ–¥–∞—Ç–∫–æ–≤–∞ —ñ–Ω—Ñ–æ—Ä–º–∞—Ü—ñ—è –ø—Ä–æ –≤–∞–ª—ñ–¥–Ω—ñ—Å—Ç—å
            aria-describedby="name-error" // –í–∫–∞–∑—É—î –Ω–∞ –µ–ª–µ–º–µ–Ω—Ç –∑ –æ–ø–∏—Å–æ–º –ø–æ–º–∏–ª–∫–∏
          />
          {!isNameValid && <p id="name-error" className={s.error} tabIndex={0}>–ü–æ–ª–µ –æ–±–æ–≤'—è–∑–∫–æ–≤–µ –¥–æ –∑–∞–ø–æ–≤–Ω–µ–Ω–Ω—è</p>}
        </label>
      </div>
      <div className={blockClass}>
        <label>
          <span>–ù–æ–º–µ—Ä —Ç–µ–ª–µ—Ñ–æ–Ω—É:</span>
          <input
            placeholder="–ù–æ–º–µ—Ä —Ç–µ–ª–µ—Ñ–æ–Ω—É"
            type="tel"
            value={patientPhone}
            onChange={handleChangePhone} // –û–±—Ä–æ–±–∫–∞ –∑–º—ñ–Ω–∏ —Ç–µ–ª–µ—Ñ–æ–Ω—É
            onBlur={validatePhone} // –í–∞–ª—ñ–¥–∞—Ü—ñ—è –ø—Ä–∏ –≤—Ç—Ä–∞—Ç—ñ —Ñ–æ–∫—É—Å—É
            required
            aria-invalid={!isPhoneValid} // –î–æ–¥–∞—Ç–∫–æ–≤–∞ —ñ–Ω—Ñ–æ—Ä–º–∞—Ü—ñ—è –ø—Ä–æ –≤–∞–ª—ñ–¥–Ω—ñ—Å—Ç—å
            aria-describedby="phone-error" // –í–∫–∞–∑—É—î –Ω–∞ –µ–ª–µ–º–µ–Ω—Ç –∑ –æ–ø–∏—Å–æ–º –ø–æ–º–∏–ª–∫–∏
          />
          {!isPhoneValid && <p id="phone-error" className={s.error} tabIndex={0}>–§–æ—Ä–º–∞—Ç –ø–æ–≤–∏–Ω–µ–Ω –±—É—Ç–∏ 380 –•–• –•–•–•–•–•–•–•</p>}
        </label>
      </div>
    </div>
  );
}

export default PatientInfo;
