// –Ü–º–ø–æ—Ä—Ç –Ω–µ–æ–±—Ö—ñ–¥–Ω–∏—Ö –º–æ–¥—É–ª—ñ–≤ —ñ –±—ñ–±–ª—ñ–æ—Ç–µ–∫
import { useEffect, useState } from 'react';
import { createPortal } from 'react-dom';
import cn from 'classnames';
import s from './Toast.module.scss';

// –ö–æ–º–ø–æ–Ω–µ–Ω—Ç –¥–ª—è –≤—ñ–¥–æ–±—Ä–∞–∂–µ–Ω–Ω—è —Å–ø–ª–∏–≤–∞—é—á–æ–≥–æ –ø–æ–≤—ñ–¥–æ–º–ª–µ–Ω–Ω—è Toast
export const Toast = ({ errorMessage }) => {
  // –°—Ç–∞–Ω –¥–ª—è –∑–±–µ—Ä—ñ–≥–∞–Ω–Ω—è —á–∞—Å—É —É —Å–µ–∫—É–Ω–¥–∞—Ö
  const [seconds, setSeconds] = useState(0);

  // –ë–∞–∑–æ–≤–∏–π –∫–ª–∞—Å –¥–ª—è Toast
  let toastClass = cn(s.toast);

  // –î–æ–¥–∞—î–º–æ –∫–ª–∞—Å –≤—ñ–¥–∫—Ä–∏—Ç–æ–≥–æ —Å—Ç–∞–Ω—É, —è–∫—â–æ seconds –±—ñ–ª—å—à–µ 0
  if (seconds > 0) {
    toastClass = cn(s.toast, {
      [s.open]: true, // –î–æ–¥–∞—î—Ç—å—Å—è –∫–ª–∞—Å –¥–ª—è –≤—ñ–¥–∫—Ä–∏—Ç–æ–≥–æ —Å—Ç–∞–Ω—É
    });
  }

  // –ó–∞–∫—Ä–∏–≤–∞—î–º–æ Toast, —è–∫—â–æ seconds –¥–æ—Å—è–≥–∞—î 2.7
  if (seconds >= 2.7) {
    toastClass = cn(s.toast, {
      [s.open]: false, // –ö–ª–∞—Å –¥–ª—è –∑–∞–∫—Ä–∏—Ç–æ–≥–æ —Å—Ç–∞–Ω—É
    });
  }

  // –í–∏–∫–æ—Ä–∏—Å—Ç–æ–≤—É—î–º–æ useEffect –¥–ª—è –æ–Ω–æ–≤–ª–µ–Ω–Ω—è seconds –∫–æ–∂–Ω—ñ 300 –º—ñ–ª—ñ—Å–µ–∫—É–Ω–¥
  useEffect(() => {
    const interval = setInterval(() => {
      setSeconds(prevSeconds => prevSeconds + 0.3); // –î–æ–¥–∞—î–º–æ 0.3 –∫–æ–∂–Ω—ñ 300 –º—Å
    }, 300);

    // –ó—É–ø–∏–Ω—è—î–º–æ —ñ–Ω—Ç–µ—Ä–≤–∞–ª –ø—Ä–∏ —Ä–æ–∑–º–æ–Ω—Ç—É–≤–∞–Ω–Ω—ñ –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∞
    return () => {
      clearInterval(interval);
    };
  }, [setSeconds]);

  // –Ø–∫—â–æ seconds –¥–æ—Å—è–≥–Ω–µ 3, –ø–æ–≤–µ—Ä—Ç–∞—î–º–æ null, —â–æ–± –Ω–µ –≤—ñ–¥–æ–±—Ä–∞–∂–∞—Ç–∏ Toast
  if (seconds >= 3) return null;

  // –ü–æ–≤–µ—Ä—Ç–∞—î–º–æ –ø–æ—Ä—Ç–∞–ª –∑ Toast –ø–æ–≤—ñ–¥–æ–º–ª–µ–Ω–Ω—è–º, —è–∫–µ –∑'—è–≤–ª—è—î—Ç—å—Å—è –≤ document.body
  return createPortal(
    <div className={toastClass}>
      <span>{errorMessage}</span>
    </div>,
    document.body,
  );
};
