// –Ü–º–ø–æ—Ä—Ç –Ω–µ–æ–±—Ö—ñ–¥–Ω–∏—Ö –º–æ–¥—É–ª—ñ–≤ —ñ –±—ñ–±–ª—ñ–æ—Ç–µ–∫
import { useContext, useState } from 'react';
import classNames from 'classnames';
import s from './DateList.module.scss';
import { UserIdContext } from './../../../context/context';

// –ö–æ–º–ø–æ–Ω–µ–Ω—Ç DateList –¥–ª—è –≤–∏–±–æ—Ä—É –¥–æ—Å—Ç—É–ø–Ω–∏—Ö –¥–∞—Ç
function DateList({ availableDates, handleSelectDate, selectedDoctor }) {
  const [activeDate, setActiveDate] = useState(null); // –°—Ç–∞–Ω –¥–ª—è –∞–∫—Ç–∏–≤–Ω–æ—ó –¥–∞—Ç–∏
  const userId = useContext(UserIdContext); // –û—Ç—Ä–∏–º–∞–Ω–Ω—è userId –∑ –∫–æ–Ω—Ç–µ–∫—Å—Ç—É

  // –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –æ–±—Ä–æ–±–∫–∏ –≤–∏–±–æ—Ä—É –¥–∞—Ç–∏
  const onClickDate = (date) => {
    setActiveDate(date); // –í—Å—Ç–∞–Ω–æ–≤–ª–µ–Ω–Ω—è –∞–∫—Ç–∏–≤–Ω–æ—ó –¥–∞—Ç–∏
    handleSelectDate(date); // –í–∏–∫–ª–∏–∫ —Ñ—É–Ω–∫—Ü—ñ—ó –¥–ª—è –æ–±—Ä–æ–±–∫–∏ –≤–∏–±–æ—Ä—É –¥–∞—Ç–∏
  };

  // –ö–ª–∞—Å–∏ –¥–ª—è –µ–ª–µ–º–µ–Ω—Ç–∞ –¥–∞—Ç–∏
  let itemClass = classNames(s.date__item, {
    [s.browser]: !userId, // –î–æ–¥–∞—î—Ç—å—Å—è –∫–ª–∞—Å, —è–∫—â–æ userId –≤—ñ–¥—Å—É—Ç–Ω—ñ–π
  });

  return (
    <div className={s.date}>
      <h2 tabIndex={0} className={s.date__title}>
        –î–æ—Å—Ç—É–ø–Ω—ñ –¥–∞—Ç–∏ –ø—Ä–∏–π–æ–º—É –ª—ñ–∫–∞—Ä—è {selectedDoctor.name}
      </h2>
      <ul className={s.date__list}>
        {availableDates.map(slot => (
          <li
            key={slot.day} // –£–Ω—ñ–∫–∞–ª—å–Ω–∏–π –∫–ª—é—á –¥–ª—è –∫–æ–∂–Ω–æ–≥–æ –µ–ª–µ–º–µ–Ω—Ç–∞ —Å–ø–∏—Å–∫—É
            onClick={() => onClickDate(slot.day)} // –û–±—Ä–æ–±–∫–∞ –∫–ª—ñ–∫—É –Ω–∞ –¥–∞—Ç—É
            onKeyDown={(e) => {
              if (e.key === 'Enter' || e.key === ' ') {
                e.preventDefault(); // –ó–∞–ø–æ–±—ñ–≥–∞–Ω–Ω—è —Å—Ç–∞–Ω–¥–∞—Ä—Ç–Ω–æ–º—É –¥—ñ—ó
                onClickDate(slot.day); // –í–∏–∫–ª–∏–∫ —Ñ—É–Ω–∫—Ü—ñ—ó –≤–∏–±–æ—Ä—É –¥–∞—Ç–∏
              }
            }}
            tabIndex={0} // –î–æ–¥–∞—î –º–æ–∂–ª–∏–≤—ñ—Å—Ç—å —Ñ–æ–∫—É—Å—É–≤–∞–Ω–Ω—è
            role="button" // –í—Å—Ç–∞–Ω–æ–≤–ª—é—î —Ä–æ–ª—å –µ–ª–µ–º–µ–Ω—Ç–∞
            className={classNames(itemClass, {
              [s.date__item_active]: slot.day === activeDate, // –ö–ª–∞—Å –¥–ª—è –∞–∫—Ç–∏–≤–Ω–æ—ó –¥–∞—Ç–∏
              [s.browser_active]: slot.day === activeDate, // –ö–ª–∞—Å –¥–ª—è –∞–∫—Ç–∏–≤–Ω–æ—ó –¥–∞—Ç–∏ –≤ –±—Ä–∞—É–∑–µ—Ä—ñ
            })}
            aria-pressed={slot.day === activeDate} // –î–æ–¥–∞—Ç–∫–æ–≤–∞ —ñ–Ω—Ñ–æ—Ä–º–∞—Ü—ñ—è –ø—Ä–æ —Å—Ç–∞–Ω
            aria-label={`–û–±–µ—Ä—ñ—Ç—å –¥–∞—Ç—É ${slot.day}`} // –û–ø–∏—Å –¥—ñ—ó –¥–ª—è assistive technologies
          >
            {slot.day} {/* –í—ñ–¥–æ–±—Ä–∞–∂–µ–Ω–Ω—è –¥–∞—Ç–∏ */}
          </li>
        ))}
      </ul>
    </div>
  );
}

export default DateList;
