// –Ü–º–ø–æ—Ä—Ç –Ω–µ–æ–±—Ö—ñ–¥–Ω–∏—Ö –º–æ–¥—É–ª—ñ–≤ —ñ –±—ñ–±–ª—ñ–æ—Ç–µ–∫
import { useContext, useState } from 'react';
import s from './TimeSlotList.module.scss';
import cn from 'classnames';
import { UserIdContext } from './../../../context/context';

// –ö–æ–º–ø–æ–Ω–µ–Ω—Ç TimeSlotList –¥–ª—è –≤–∏–±–æ—Ä—É –¥–æ—Å—Ç—É–ø–Ω–∏—Ö —á–∞—Å–æ–≤–∏—Ö —Å–ª–æ—Ç—ñ–≤
function TimeSlotList({ availableSlots, handleSelectSlot, selectedDate }) {
  const [activeSlot, setActiveSlot] = useState(null); // –°—Ç–∞–Ω –¥–ª—è –∑–±–µ—Ä–µ–∂–µ–Ω–Ω—è –∞–∫—Ç–∏–≤–Ω–æ–≥–æ —Å–ª–æ—Ç—É
  const userId = useContext(UserIdContext); // –û—Ç—Ä–∏–º–∞–Ω–Ω—è userId –∑ –∫–æ–Ω—Ç–µ–∫—Å—Ç—É

  // –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –æ–±—Ä–æ–±–∫–∏ –≤–∏–±–æ—Ä—É —á–∞—Å–æ–≤–æ–≥–æ —Å–ª–æ—Ç—É
  const onClickSlot = (slot) => {
    setActiveSlot(slot); // –û–Ω–æ–≤–ª–µ–Ω–Ω—è –∞–∫—Ç–∏–≤–Ω–æ–≥–æ —Å–ª–æ—Ç—É
    handleSelectSlot(slot); // –í–∏–∫–ª–∏–∫ —Ñ—É–Ω–∫—Ü—ñ—ó –¥–ª—è –æ–±—Ä–æ–±–∫–∏ –≤–∏–±–æ—Ä—É
  };

  // –ö–ª–∞—Å–∏ –¥–ª—è –µ–ª–µ–º–µ–Ω—Ç–∞ —Å–ø–∏—Å–∫—É —á–∞—Å–æ–≤–∏—Ö —Å–ª–æ—Ç—ñ–≤
  const itemClass = cn(s.time__item, {
    [s.browser]: !userId, // –î–æ–¥–∞—Ç–æ–∫ –∫–ª–∞—Å—É, —è–∫—â–æ userId –≤—ñ–¥—Å—É—Ç–Ω—ñ–π
  });

  return (
    <div className={s.time}>
      <h2 tabIndex={0} className={s.time__title}>–î–æ—Å—Ç—É–ø–Ω—ñ —á–∞—Å–∏ –ø—Ä–∏–π–æ–º—É {selectedDate}</h2>
      <ul className={s.time__list}>
        {availableSlots.map(slot => (
          <li
            key={slot.time}
            onClick={() => onClickSlot(slot.time)} // –û–±—Ä–æ–±–∫–∞ –∫–ª—ñ–∫—É
            onKeyDown={(e) => {
              if (e.key === 'Enter' || e.key === ' ') {
                e.preventDefault(); // –ó–∞–ø–æ–±—ñ–≥–∞–Ω–Ω—è —Å—Ç–∞–Ω–¥–∞—Ä—Ç–Ω–æ—ó –ø–æ–≤–µ–¥—ñ–Ω–∫–∏
                onClickSlot(slot.time); // –û–±—Ä–æ–±–∫–∞ –∫–ª–∞–≤—ñ–∞—Ç—É—Ä–Ω–æ–≥–æ –≤–≤–æ–¥—É
              }
            }}
            tabIndex={0} 
            role="button" 
            className={cn(itemClass, {
              [s.time__item_active]: slot.time === activeSlot, // –î–æ–¥–∞—Ç–æ–∫ –∫–ª–∞—Å—É –¥–ª—è –∞–∫—Ç–∏–≤–Ω–æ–≥–æ —Å–ª–æ—Ç—É
              [s.browser_active]: slot.time === activeSlot,
            })}
            aria-pressed={slot.time === activeSlot} // –î–æ–¥–∞—Ç–∫–æ–≤–∞ —ñ–Ω—Ñ–æ—Ä–º–∞—Ü—ñ—è –ø—Ä–æ –∞–∫—Ç–∏–≤–Ω—ñ—Å—Ç—å
            aria-label={`–û–±–µ—Ä—ñ—Ç—å —á–∞—Å ${slot.time}`} // –û–ø–∏—Å –¥–ª—è –∞—Å–∏—Å—Ç–∏–≤–Ω–∏—Ö —Ç–µ—Ö–Ω–æ–ª–æ–≥—ñ–π
          >
            {slot.time}
          </li>
        ))}
      </ul>
    </div>
  );
}

export default TimeSlotList;
