// –Ü–º–ø–æ—Ä—Ç –Ω–µ–æ–±—Ö—ñ–¥–Ω–∏—Ö –º–æ–¥—É–ª—ñ–≤ —ñ –±—ñ–±–ª—ñ–æ—Ç–µ–∫
import { useContext, useState } from 'react';
import classNames from 'classnames';
import s from './DoctorList.module.scss';
import { UserIdContext } from '../../../context/context';

// –ö–æ–º–ø–æ–Ω–µ–Ω—Ç DoctorList –¥–ª—è –≤–∏–±–æ—Ä—É –ª—ñ–∫–∞—Ä—è –∑–∞ —Å–ø–µ—Ü—ñ–∞–ª—ñ–∑–∞—Ü—ñ—î—é
function DoctorList({ doctors, handleClickDoctor, selectedSpecialization }) {
  const [activeDoctor, setActiveDoctor] = useState(null); // –°—Ç–∞–Ω –¥–ª—è –∞–∫—Ç–∏–≤–Ω–æ–≥–æ –ª—ñ–∫–∞—Ä—è
  const userId = useContext(UserIdContext); // –û—Ç—Ä–∏–º–∞–Ω–Ω—è userId –∑ –∫–æ–Ω—Ç–µ–∫—Å—Ç—É

  // –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –æ–±—Ä–æ–±–∫–∏ –≤–∏–±–æ—Ä—É –ª—ñ–∫–∞—Ä—è
  const onClickDoctor = (doctor) => {
    setActiveDoctor(doctor._id); // –í—Å—Ç–∞–Ω–æ–≤–ª–µ–Ω–Ω—è –∞–∫—Ç–∏–≤–Ω–æ–≥–æ –ª—ñ–∫–∞—Ä—è
    handleClickDoctor(doctor); // –í–∏–∫–ª–∏–∫ —Ñ—É–Ω–∫—Ü—ñ—ó –¥–ª—è –æ–±—Ä–æ–±–∫–∏ –≤–∏–±–æ—Ä—É –ª—ñ–∫–∞—Ä—è
  };

  // –ö–ª–∞—Å–∏ –¥–ª—è –µ–ª–µ–º–µ–Ω—Ç–∞ –ª—ñ–∫–∞—Ä—è
  const itemClass = classNames(s.doctor__item, {
    [s.browser]: !userId, // –î–æ–¥–∞—î—Ç—å—Å—è –∫–ª–∞—Å, —è–∫—â–æ userId –≤—ñ–¥—Å—É—Ç–Ω—ñ–π
  });

  return (
    <div className={s.doctor}>
      <h2 tabIndex={0} className={s.doctor__title}>
        –õ—ñ–∫–∞—Ä—ñ –∑–∞ —Å–ø–µ—Ü—ñ–∞–ª—ñ–∑–∞—Ü—ñ—î—é {selectedSpecialization}
      </h2>
      <ul className={s.doctor__list}>
        {doctors.map(doctor => (
          <li
            key={doctor._id} // –£–Ω—ñ–∫–∞–ª—å–Ω–∏–π –∫–ª—é—á –¥–ª—è –∫–æ–∂–Ω–æ–≥–æ –µ–ª–µ–º–µ–Ω—Ç–∞ —Å–ø–∏—Å–∫—É
            onClick={() => onClickDoctor(doctor)} // –û–±—Ä–æ–±–∫–∞ –∫–ª—ñ–∫—É –Ω–∞ –ª—ñ–∫–∞—Ä—è
            onKeyDown={(e) => {
              if (e.key === 'Enter' || e.key === ' ') {
                e.preventDefault(); // –ó–∞–ø–æ–±—ñ–≥–∞–Ω–Ω—è —Å—Ç–∞–Ω–¥–∞—Ä—Ç–Ω–æ–º—É –¥—ñ—ó
                onClickDoctor(doctor); // –í–∏–∫–ª–∏–∫ —Ñ—É–Ω–∫—Ü—ñ—ó –≤–∏–±–æ—Ä—É –ª—ñ–∫–∞—Ä—è
              }
            }}
            tabIndex={0} // –î–æ–¥–∞—î –º–æ–∂–ª–∏–≤—ñ—Å—Ç—å —Ñ–æ–∫—É—Å—É–≤–∞–Ω–Ω—è
            role="button" // –í—Å—Ç–∞–Ω–æ–≤–ª—é—î —Ä–æ–ª—å –µ–ª–µ–º–µ–Ω—Ç–∞
            className={classNames(itemClass, {
              [s.doctor__item_active]: doctor._id === activeDoctor, // –ö–ª–∞—Å –¥–ª—è –∞–∫—Ç–∏–≤–Ω–æ–≥–æ –ª—ñ–∫–∞—Ä—è
              [s.browser_active]: doctor._id === activeDoctor, // –ö–ª–∞—Å –¥–ª—è –∞–∫—Ç–∏–≤–Ω–æ–≥–æ –ª—ñ–∫–∞—Ä—è –≤ –±—Ä–∞—É–∑–µ—Ä—ñ
            })}
            aria-pressed={doctor._id === activeDoctor} // –î–æ–¥–∞—Ç–∫–æ–≤–∞ —ñ–Ω—Ñ–æ—Ä–º–∞—Ü—ñ—è –ø—Ä–æ —Å—Ç–∞–Ω
            aria-label={`–û–±–µ—Ä—ñ—Ç—å –ª—ñ–∫–∞—Ä—è ${doctor.name}`} // –û–ø–∏—Å –¥—ñ—ó –¥–ª—è assistive technologies
          >
            {doctor.name} {/* –í—ñ–¥–æ–±—Ä–∞–∂–µ–Ω–Ω—è —ñ–º–µ–Ω—ñ –ª—ñ–∫–∞—Ä—è */}
          </li>
        ))}
      </ul>
    </div>
  );
}

export default DoctorList;
